export default {
	data() {
		return {
			choose: '',
			arr: ['']
		}
	},
	methods: {
		deleteNum(e) {
			console.log(e)
			var a = e.currentTarget.dataset.info;

			var c = [];
			for (let i = 0; i < this.arr.length; i++) {
				if (i != a) {
					c.push(this.arr[i])
				}
			}
			console.log(c)
			this.arr = c;

		},
		textareaBInput(e) {
			console.log(e)
			var a = e.currentTarget.dataset.info;
			this.arr[a] = e.detail.value;
		},
		add() {
			this.arr.push('')
		},
		showMoadel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		commit() {
			console.log(this.arr)

			var self = this;
			if (!this.arr.every((item, index, array) => {
					return item != '';
				})) {
				self.showMoadel('请输入完整内容！');
			} else {
				var content = self.arr.join("*");
				uni.showLoading({
					title: '提交中。。。',
					mask: true
				})
				setTimeout(() => {
					uni
						.request({
							url: `${self.$store.state.urlPrefix}/home/Add_JournalAccount`,
							data: {
								contents: content,
								userid: self.$store.state.userInfo.RYXH,
								XZCD: self.choose
							}
						})
						.then(data => {
							var [error, res] = data;

							uni.hideLoading();

							if (res.data.code == 0) {
								uni.showModal({
									title: '提示',
									content: "添加成功",
									showCancel: false,
									success: function(res) {
										if (res.confirm) {
											uni.navigateBack();
										}
									}
								});
							} else {
								self.showMoadel('添加失败')
							}
							self.arr = ['']
						});
				}, 1000)
			}
		},
	},
	onLoad(option) {
		console.log(option)
		this.choose = option.id;
	}
}
